$colorMain: #336799;

.imgLogo{
    height: 50px;
    width: 50px;
    margin-right: 25px;
}

.navBarCustom{
    height: 65px;
    background-color: #cecece !important;
}

.nav-link{
    color:#336799;
    font-size: 1.075em;
    font-weight: 400;
    margin-right: 5px;
    transition: .3s ease-in-out;
    &:hover{
        color: #008bcc;
        transition: .3s ease-in-out;
    }
}

.dropdown-item{
    color:#336799;
    font-size: 1.075em;
    font-weight: 400;
    transition: .3s ease-in-out;
    &:hover{
        color: #008bcc;
        transition: .3s ease-in-out;
    }
}

.btnContactanos{
    border: 1px solid $colorMain !important;
    color: #336799 !important;
    font-size: 1.075em !important;
    font-weight: 400 !important;
    transition: .3s ease-in-out !important;
    &:hover{
       border: 1px solid #008bcc !important;
       background-color: #008bcc !important;
       color: rgb(255, 255, 255) !important;
       transition: .3s ease-in-out !important;
    }
}

@media (max-width:1000px) {
    .navBarCustom{
        height: auto;
    }
}

@media (max-width:480px) {
    .navBarCustom{
        height: auto;
    }

    .dropdown-item{
        color:#336799;
        font-size: .85em;
        font-weight: 400;
        transition: .3s ease-in-out;
        &:hover{
            color: #008bcc;
            transition: .3s ease-in-out;
        }
    }
   
}