.containerCasosExito {
  position: relative;
  height: auto;
  width: 100% !important;
  background-color: #008bcc;

  .titleCasosExito {
    position: relative;
    width: 75%;

    .titleCasos {
      color: white;
      font-weight: 600 !important;
      font-size: 2.5em;
    }

    .subtitleCasos {
      color: white;
      font-size: 1.15em;
    }
  }
  .resenasContainerManin {
    display: flex;
    flex-wrap: wrap;
    margin-top: 25px;
    margin-bottom: 25px;
    gap: 20px;
    .resena {
      position: relative;
      height: 410px;
      width: 400px;
      border: 2px solid rgba(255, 255, 255, 0.753);
      border-radius: 20px;
      display: flex;
      justify-content: start;
      align-items: start;
      flex-direction: column;
      padding: 0px 25px;
      .titleResena {
        color: white;
        font-size: 1.7em;
      }
      .containerStars {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 18px;
        .iconoEstilo {
          color: rgb(246, 162, 0);
          font-size: 20px;
        }
      }
      .containerDialogImg {
        ion-icon {
          background-color: transparent;
          height: 40px;
          width: 40px;
          color: white;
        }
      }
      .containerOpinion {
        .textOpinion {
          color: white;
        }
      }
    }
  }
}

