$colorMain: #008bcc;

#porqueE{
    background-color: #008bcc;
}

.titlePorqueE{
    color:#ffffff;
}
.containerCards {
    margin-top: 100px !important;
    position: relative;
    width: 100% !important;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 70px;
    z-index: 0;

    .containerCard {
        position: relative;
        height: 240px !important;
        width: 350px !important;
        border: 1px solid #ffffff;
        padding: 5px 10px !important;
        background-color: white !important;
        border: 1px solid #ff00001e;
        border-radius: 15px;
        box-shadow: 3px 3px 15px rgba(255, 255, 255, 0.603);
       
        .imgCard{
            width: 75px;
            margin-top: -50px;
            border-radius: 50%;
        }

        .containerCardText{
            margin-top: 25px;
            .firstText{
                font-size: 1.3em;
                color: black;
            }
            .textIdentificador{
                font-size: .85em;
            }
        }
      }
  }

  @media screen and (max-width: 480px){
    .containerCards {
        margin-top: 100px !important;
        position: relative;
        width: 100% !important;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        gap: 70px;
        z-index: 0;
    
        .containerCard {
            position: relative;
            height: 230px !important;
            width: 350px !important;
            border: 1px solid #ffffff;
            padding: 5px 10px !important;
            background-color: white !important;
           
            .imgCard{
                width: 75px;
                margin-top: -50px;
            }
    
            .containerCardText{
                margin-top: 25px;
                .firstText{
                    font-size: 1.35em;
                    color: #185f97;
                }
                .textIdentificador{
                    font-size: .9em;
                }
            }
          }
      }
  }
  