.title {
  font-size: 1.65em;
  text-align: center;
  margin-top: 50px;
}
.cursosContainer {
  display: flex;
  height: 450px;
  margin-top: 15px;
  flex-direction: row;

  .imgContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 50%;
    box-sizing: border-box;
    padding: 10px 15px;
    img {
      width: 90%;
      box-shadow: 5px 5px 15px rgba(197, 198, 255, 0.1);
    }
  }

  .infoCursoContainer {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: start;
    flex-direction: column;
    width: 50%;
    box-sizing: border-box;
    padding: 10px 40px;
    .titleService {
      font-size: 1.5em;
    }
    .textService {
      margin-top: 30px;
      font-size: 1em;
    }
    .btnServiceContainer {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 60% !important;
      margin-top: 40px;
      .btnService {
        width: 100%;
        height: 45px;
        background-color: #008bcc;
        box-shadow: 5px 5px 10px rgba(255, 255, 255, 0.2);
        color: white;
        letter-spacing: 1px;
        transition: 0.3s ease-in-out;
        &:hover {
          background-color: #3298fd;
          box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.258);
          transition: 0.3s ease-in-out;
        }
      }
    }
  }
}

#servicio2,
#servicio4,
#servicio6 {
  flex-direction: row-reverse !important;
  background: rgb(206, 206, 206);
}

#textService2, #textService4, #textService6 {
  color: rgb(80, 80, 80) !important;
}

#titleService2, #titleService4, #titleService6 {
  color: rgb(80, 80, 80) !important;
}

@media screen and (max-width: 768px) {
  .title {
    font-size: 1.65em;
    text-align: center;
    margin-top: 25px;
  }
  .cursosContainer {
    display: flex;
    height: auto;
    flex-direction: column;
    margin-top: -25px !important;

    .imgContainer {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 100%;
      box-sizing: border-box;
      padding: 10px 15px;
      margin-top: 40px;
      img {
        width: 90%;
      }
    }

    .infoCursoContainer {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 100%;
      box-sizing: border-box;
      padding: 10px 25px;
      margin-top: 10px;
      .titleService {
        font-size: 1.5em;
        text-align: center;
      }
      .textService {
        margin-top: 15px;
        font-size: 1em;
        text-align: center;
        color: rgb(79, 76, 76) !important;
      }
      .btnServiceContainer {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 80% !important;
        margin-top: 40px;
        margin-bottom: 25px;
        .btnService {
          width: 100%;
          height: 45px;
          background-color: #008bcc;
          box-shadow: 5px 5px 10px rgba(255, 255, 255, 0.1);
          color: white;
          letter-spacing: 1px;
          transition: 0.3s ease-in-out;
          &:hover {
            background-color: #3298fd;
            box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.258);
            transition: 0.3s ease-in-out;
          }
        }
      }
    }
  }

  #servicio1 {
    margin-bottom: 20px !important;
  }

  #servicio2,
  #servicio4,
  #servicio6 {
    flex-direction: column !important;
    background-color: rgb(248, 249, 250);
    border: 1px solid rgba(204, 207, 255, 0.05);
  }

  #titleService1,
  #titleService2,
  #titleService3,
  #titleService4,
  #titleService5,
  #titleService6,
  #titleService7 {
    text-align: center !important;
    display: flex;
    justify-content: center;
  }
}
